<template>
    <ion-page>
        <ion-content>
            <ion-card class="ion-padding">
                <ion-card-header>
                    <ion-img style="margin:0px auto;width:10%" src="assets/Malawi-Coat_of_arms_of_arms.png"></ion-img>
                    <ion-card-title style="text-transform: uppercase; margin: 10px;" class="ion-text-center his-lg-text">
                        IBLIS Reception
                    </ion-card-title>
                    <ion-card-subtitle class="ion-text-center his-sm-text">
                        Login
                    </ion-card-subtitle>
                </ion-card-header>
                <ion-card-content>
                    <ion-item class="ion-padding his-sm-text">
                        <ion-label><b>Username:</b></ion-label>
                        <ion-input @ion-focus="enterInput('username')" v-model="inputs.username" :value="inputs.username" placeholder="Username">
                        </ion-input>
                    </ion-item>
                    <ion-item class="ion-padding his-sm-text">
                        <ion-label><b>Password:</b></ion-label>
                        <ion-input v-model="inputs.password" :value="inputs.password" @ion-focus="enterInput('password')" type="password" placeholder="************">
                        </ion-input>
                    </ion-item>
                    <ion-button :disabled="!isset" @click="submit" size="large" expand="block">
                        Submit
                    </ion-button>
                </ion-card-content>
            </ion-card>
            <div class="keyboard">
                <SimpleKeyboard
                v-show="showKeyboard && (inputName === 'username' || inputName === 'password')"
                @onChange="onChange"
                @onKeyPress="onKeyPress"
                :input="inputs[inputName]"
                :inputName="inputName"
            />
            </div>
        </ion-content>
    </ion-page>
</template>
<script setup lang="ts">
import {
    IonImg,
    IonCard,
    IonCardTitle,
    IonCardContent,
    IonCardHeader,
    IonItem,
    IonLabel,
    IonInput,
    IonButton,
    IonPage,
    IonContent,
    IonCardSubtitle
} from "@ionic/vue"
import { ref, computed } from "vue";
import { Client } from "@/composables/client"
import router from "@/router";
import SimpleKeyboard from "@/components/Keyboard/SimpleKeyBoard.vue"

const client = Client()
const username = ref<string>()
const password = ref<string>()
const isAuthenticating = ref<boolean>(false)
const isset = computed(() => !isAuthenticating.value && inputs.value.username && inputs.value.password)
const showKeyboard = ref<boolean>(false)
const inputs = ref<credentials>({ username: "", password: "" })
const inputName = ref<fields>("username")

type credentials = {
    username: string;
    password: string;
}
type fields = "username" | "password";

const submit = () => {
    isAuthenticating.value = true
    client.authenticate({
        username: inputs.value.username || '',
        password: inputs.value.password || '',
        department: 'Lab Reception'
    }).then((ok) => {
        if (ok) {
            username.value = ''
            password.value = ''
            router.push('/locations')
        }
        isAuthenticating.value = false
    })
        .catch(() => isAuthenticating.value = false)
}

const enterInput = (value: fields) => {
    inputName.value = value
    if(value == "username" || value == "password"){
        showKeyboard.value = true
    }else { showKeyboard.value = false }
}

const onChange = (input: string) => {
    console.log(input)
    inputs.value[inputName.value] = input
}

const onKeyPress = (input: string) => {
    input == "{enter}" && (submit())
}
</script>
<style scoped>
.keyboard {
    width: 100%;
    bottom: 0;
    box-shadow: 100px;
    position: absolute !important;
    font-size: 30px;
    text-transform: uppercase;
    font-weight: 500;
  }
</style>
